import React from 'react';
import './exp.css';
import DeleteIcon from '@mui/icons-material/Delete';
import CreateIcon from '@mui/icons-material/Create';

const Exp = ({all,Showingfunction,deletee,setinput,setcheck}) => {
    const del = (index) => {
         deletee(index);
         Showingfunction("success","Item Removed Successfully!")
    }
    const edie = (index) => {
        setinput({
            ind: index,
            head: all[index].head,
            amt: all[index].amt,
            ledger: all[index].ledger
        })
        setcheck(true);
    }
    return (
        <>
            { all.map((all, ind) => {
                return <div key={ind} className={all.head == "exp" ? "exp" : "income"}> <span className='sno'>{ind + 1}</span>. <span> {all.head == "exp" ? "Expense" : "Income"}</span> <span> {all.head == "exp" ? "-" : "+"} {all.amt} </span> <span> {all.ledger} </span> <DeleteIcon titleAccess='Delete' title="Delete" onClick={() => { del(ind) }} className='del' /> <CreateIcon  titleAccess='Edit' onClick={() => { edie(ind) }} className='edi' /> </div>
            }).reverse()}
           
        </>
    )
}
export default Exp;