import React from 'react'
import { useState } from 'react';
import TextField from '@mui/material/TextField';
import axios from 'axios';
import Button from '@mui/material/Button';
import SendIcon from '@mui/icons-material/Send';

const Signupe = ({ setreq }) => {
    const init = {
        name: "",
        email: "eve.holt@reqres.in",
        password: ""
    }
    const [inp, setinp] = useState(init)

    const handleChange = (e) => {
        let field = e.target.name;
        let value = e.target.value;
        setinp((prev) => {
            return {
                ...prev,
                [field]: value
            }
        })
    }
    const submit = async () => {
        if (inp.name == "" || inp.email == "" || inp.password == "") {
            alert("All Fields are Required!");
        } else {
            // axios.post('https://reqres.in/api/register', {
            //     email: inp.email,
            //     password: inp.password,
            // }).then((result) => {
            //     console.log(result);
            //     console.log(result.data.id);
            //     alert("Signup Successfully Token: " + result.data.token);
            //     setreq(false);
            //     setinp(init);
            //     localStorage.setItem("token", result.data.token);
            // }).catch((err) => {
            //     console.log(err);
            // })
            const res = await axios.post('https://reqres.in/api/register', {
                email: inp.email,
                password: inp.password,
            });
            if (res.status == 200) {
                alert("Signup Successfully Token: " + res.data.token);
                setreq(false);
                setinp(init);
                localStorage.setItem("token", res.data.token);
            } else {
                console.log(res.status);
                alert("Something went wrong")
            }
        }

    }
    return (
        <>
            <TextField
                label="Name*"
                id="outlined-start-adornment"
                sx={{ m: 1, minWidth: 160 }}
                size="small"
                onChange={handleChange}
                name="name"
                value={inp.name}
            />
            <TextField
                label="Email*"
                id="outlined-start-adornment"
                sx={{ m: 1, minWidth: 160 }}
                size="small"
                onChange={handleChange}
                name="email"
                value={inp.email}
            />
            <TextField
                label="Password*"
                id="outlined-start-adornment"
                sx={{ m: 1, minWidth: 160 }}
                size="small"
                onChange={handleChange}
                name="password"
                value={inp.password}
            />
            <br />
            {/* <button title='Signup' onClick={submit}>SignUp</button> */}
            <Button title='Signup' onClick={submit} variant="contained" endIcon={<SendIcon />}>
                Signup
            </Button>
        </>
    )
}
export default Signupe;