import React, { useState, useEffect } from 'react'
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import TextField from '@mui/material/TextField';
import InputAdornment from '@mui/material/InputAdornment';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

const Addexp = ({ input, setinput, all, setall, check, setcheck, Showingfunction }) => {
    const init = {
        head: "",
        ledger: "",
        amt: ""
    }
    const expledger = ["General", "Petrol", "Ration", "Other"]
    const incledger = ["Salary", "Online", "Other"]


    const handleChange = (e) => {
        let name = e.target.name;
        let value = e.target.value;
        setinput((prev) => {
            return {
                ...prev,
                [name]: value,
            }
        })
    }
    const adddata = () => {
        if (input.amt == "" || input.head == "" || input.ledger == "") {
            Showingfunction("warning", "All Fields are Required! ")
        } else {
            setall((prev) => {
                return [...prev, input]
            })
            setinput(init);
            Showingfunction("success", "Item Inserted Successfully!")
        }
    }

    useEffect(() => {
        localStorage.setItem("entry", JSON.stringify(all));
    }, [all])

    const valueUpdate = (index) => {
        setall(
            all.map((elem, ind) => {
                if (ind === index) {
                    return { ...elem, amt: input.amt, head: input.head, ledger: input.ledger }
                }
                return elem;
            })
        )
        setinput(init);
        setcheck(false);
        Showingfunction("success", "Item Edited Successfully!")
    }

    return (
        <>
            <div className="command">
                <TextField
                    label="Amount*"
                    id="outlined-start-adornment"
                    sx={{ m: 1, minWidth: 100, maxWidth: 120 }}
                    size="small"
                    onChange={handleChange}
                    name="amt"
                    value={input.amt}
                    onKeyPress={(event) => { if (!/[0-9]/.test(event.key)) { event.preventDefault(); } }}
                    InputProps={{
                        startAdornment: <InputAdornment position="start">Rs.</InputAdornment>,
                    }}
                />
                <FormControl sx={{ m: 1, minWidth: 100 }} size="small" required>
                    <InputLabel id="head">Head</InputLabel>
                    <Select
                        labelId="head"
                        id="head"
                        value={input.head}
                        label="head"
                        name="head"
                        onChange={handleChange}
                    >
                        <MenuItem value="income">Income</MenuItem>
                        <MenuItem value="exp">Expense</MenuItem>
                    </Select>
                </FormControl>

                <FormControl sx={{ m: 1, borderRadius: '50%', minWidth: 100, maxWidth: 150 }} size="small" required>
                    <InputLabel id="ledgere">Ledger</InputLabel>
                    <Select
                        labelId="ledgere"
                        id="ledgere"
                        value={input.ledger}
                        label="ledgere"
                        name="ledger"
                        onChange={handleChange}
                    >
                        {input.head == "exp" ? expledger.map((elem, ind) => <MenuItem key={ind} value={elem}>{elem}</MenuItem>) : incledger.map((elem, ind) => <MenuItem key={ind} value={elem}>{elem}</MenuItem>)}
                    </Select>
                </FormControl>
                {check ? <EditIcon titleAccess='Edit Value' fontSize='medium' color="primary" style={{ padding: "4px" }} className='add' onClick={() => valueUpdate(input.ind)} /> : <AddIcon titleAccess='Add New Entry' className='add' onClick={adddata} />}
            </div>
        </>
    )

}

export default Addexp;