import React, { useState } from 'react'

const Alluser=()=>{
   
const [user,setuser]=useState([{
    name:"jai",
    _id:"test",
    email:""
}])
 const hello= async ()=>{
     const res = await fetch('/user', {
        method:"POST",
        headers:{
            "Content-Type": "application/json"
        }
     })
     console.log(res.status)
     const data = await res.json();
     console.log(data.msg);
     setuser(data.msg);
    
 }

   return(
    <>
            <div className='allcontent'>
                <div>
                    <button onClick={hello}>Fetch</button> total user: {user.length}
                    {user.map((val,ind)=>{
                     return <div key={val._id}>Id: {val._id} , name: {val.name} , email: {val.email}</div>
                    })}
                    </div>
            </div>

    </>
   )
}

export default Alluser;