import React from 'react'

const Ul = (param) => {
    return (
        <ul>
            {param.list.map((itemval, ind) => {
                return <li key={ind}><i className="fa fa-trash" onClick={() => param.handl(ind)} id={ind} aria-hidden="true"></i> <span>{itemval}</span></li>
            })}
        </ul>
    )
}
export default Ul;