import React, { useState } from 'react'
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import SendIcon from '@mui/icons-material/Send';
import axios from 'axios';

const Contact = ({setlogin}) => {

    const [input, setinp] = useState({
        name: "",
        email: "",
        contact: "",
        comment: ""
    })
    const handleChange = (e) => {
        let naam = e.target.name;
        let val = e.target.value;
        setinp({ ...input, [naam]: val })
    }
    const fetchene = async () => {
        const res = await fetch('https://learning-df2ab-default-rtdb.firebaseio.com/testingjai.json/NH8e834W3ESV9LQhqwR', {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
            }
        });
        console.log(res);
    }
    const submit = () => {
        const { name, email, contact, comment } = input;
        if (name && email && contact && comment) {
            const fetche = async () => {
                const res = await fetch('https://learning-df2ab-default-rtdb.firebaseio.com/testingjai.json',
                    {
                        method: "POST",
                        headers: {
                            "Content-Type": "application/json",
                        },
                        body: JSON.stringify({
                            name,
                            email,
                            contact,
                            comment,
                        }),
                    })
                if (res) {
                    alert("Data Stored in firebase");
                    setinp({
                        name: "",
                        email: "",
                        contact: "",
                        comment: ""
                    })
                }
            }
            fetche();
        } else {
            alert("All fields are Required!");
        }
    }
    return (
        <>
            <div className='allcontent'>
                <div>
                    <TextField
                        label="Name*"
                        id="outlined-start-adornment"
                        sx={{ m: 1, minWidth: 100, maxWidth: 120 }}
                        size="small"
                        onChange={handleChange}
                        name="name"
                        value={input.name}
                    />
                    <TextField
                        label="Email*"
                        id="outlined-start-adornment"
                        sx={{ m: 1, minWidth: 100, maxWidth: 120 }}
                        size="small"
                        onChange={handleChange}
                        name="email"
                        value={input.email}
                    />
                    <TextField
                        label="Contact*"
                        id="outlined-start-adornment"
                        sx={{ m: 1, minWidth: 100, maxWidth: 120 }}
                        size="small"
                        onChange={handleChange}
                        name="contact"
                        value={input.contact}
                    />
                    <TextField
                        label="Comment*"
                        id="outlined-start-adornment"
                        sx={{ m: 1, minWidth: 100, maxWidth: 120 }}
                        size="large"
                        onChange={handleChange}
                        name="comment"
                        value={input.comment}
                    />
                    <Button title='Signup' onClick={submit} variant="contained" endIcon={<SendIcon />}>
                        Submit
                    </Button>
                    <Button title='Signup' onClick={fetchene} variant="contained" endIcon={<SendIcon />}>
                        Fetching
                    </Button>
                </div>
            </div>
        </>
    )
}
export default Contact;