import React, { useEffect } from 'react';
import Gitcity from './gitcity';
import FormControl from '@mui/material/FormControl';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import { useState } from 'react';

const Citye = ({ setlogin }) => {
    const [city, setcity] = useState("");
    const [state, setstate] = useState("Bihar");

    const statehandle = (e) => {
        setstate(e.target.value);
    }

    const allstate = Gitcity.map((val, ind) => {
        return val.state
    })

    function unique(val,ind,arr){
        return arr.indexOf(val) === ind;
    }

    let cstat = allstate.filter(unique);
    // console.log(c);

    // const newstate = new Set(allstate);

    // console.log(newstate);

    const cityhandle = (e) => {
        setcity(e.target.value);
    }

    useEffect(() => {
    //    newstate.map((val,ind)=>{
    //     console.log(val);
    //    })
    }, []);
    return (
        <>
            <div className='allcontent'>
                <FormControl sx={{ m: 1, minWidth: 100 }} size="small" required>
                    <InputLabel id="head">State</InputLabel>
                    <Select
                        labelId="head"
                        id="head"
                        value={state}
                        label="head"
                        name="head"
                        onChange={statehandle}
                    >
                        {
                            cstat.map((val, ind) => {
                                    return <MenuItem key={ind} value={val}> {val}</MenuItem>;
                            })
                        }
                    </Select>
                </FormControl>
                <FormControl sx={{ m: 1, minWidth: 100 }} size="small" required>
                    <InputLabel id="head">City</InputLabel>
                    <Select
                        labelId="head"
                        id="head"
                        value={city}
                        label="head"
                        name="head"
                        onChange={cityhandle}
                    >
                        {
                            Gitcity.map((val, ind) => {
                            //   (  val.state == state ?  <MenuItem key={ind} value={val.name}> {val.name} </MenuItem> : "NIL")
                                if (val.state === state) {
                                    return <MenuItem key={ind} value={val.name}> {val.name}</MenuItem>;
                                }
                            })
                        }
                    </Select>
                </FormControl>
            </div>
        </>
    )
}
export default Citye;