import React, { useState } from 'react'
import Signin from './signin';
import Signupe from './signup1';

const Signup = ({ setlogin }) => {
    const [req, setreq] = useState(false);
    const css = {
        background: " rgb(6, 99, 106)",
        color: "white"
    }
    return (
        <>
            <div className='allcontent'>
                <div className="container">
                    <h1>Sign Up / Sign In</h1>
                    <div className="toogle"><span style={req ? null : css} className="here" to='/' onClick={() => setreq(false)}>SingIn</span> <span style={req ? css : null} onClick={() => setreq(true)} className="here" to='/signin' >Signup</span></div>
                    {req ? <Signupe setreq={setreq} /> : <Signin setlogin={setlogin} />}
                </div>
            </div>
        </>
    )
}
export default Signup;