import React, { useState } from 'react'
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

export default function FormDialog({setuser,open,setOpen,Showingfunction}) {
    const [userinp, setuserinp] = useState("");
   
    const handleChange = (e) => {
        let value = e.target.value;
        setuserinp(value);
    }
    const addname = () => {
        if(userinp!==""){
            localStorage.setItem("username", JSON.stringify(userinp));
            setuser(userinp);
            setOpen(false);
        }else{
            Showingfunction("warning", "Name Can't be Blank!");
        }
    }
    return (
        <div>
            <Dialog open={open} >
                <DialogTitle>Welcome to Expense Manager</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        Kindly Enter Your Name to help us to provide better service to you.
                    </DialogContentText>
                    <TextField
                        autoFocus
                        name="username"
                        onChange={handleChange}
                        value={userinp}
                        margin="dense"
                        id="name"
                        label="Your Name..."
                        type="email"
                        fullWidth
                        variant="standard"
                    />
                </DialogContent>
                <DialogActions>
                    <Button onClick={addname}>Submit</Button>
                </DialogActions>
            </Dialog>
            {/* {this.fune()}; */}
        </div>

    );
}