import React, { useState } from 'react'
import Ul from './ul';

const Todo = () => {
    const [hero, sethero] = useState("");
    const [koi, setkoi] = useState([]);
    const Handle = (e) => {
        sethero(e.target.value);
    }
    const Even = () => {
        setkoi((oldval) => {
            return [...oldval, hero]
        })
        sethero("");
    }

    const Delete=(ide)=>{
        console.log(ide);
        setkoi((oldvale) => {
           return oldvale.filter((arr, inde)=>{
               return ide !== inde;
           })
        })
       
    }

    return (
        <>
            <div className='allcontent'>
            <div className="box">
                <div><h1>To-Do List</h1></div>
                <div><input type="text" value={hero} onChange={Handle} /> <i className="fa fa-plus" onClick={Even} aria-hidden="true"></i></div>
                <div>
                    <Ul list={koi} handl={Delete}/>
                </div>
            </div>
            </div>
        </>
    )
}
export default Todo;