import React, { useEffect } from 'react'
import './home.css'
import { useNavigate } from 'react-router-dom';

const Home = ({ islogin ,setlogin }) => {
  const navigate = useNavigate();
  useEffect(() => {
    if (!localStorage.getItem('token')) {
      navigate('/signup');
    } else {

    }
  });
  return (
    <>
      <div className='allcontent'>
        <div className="all">
          <div className="username">
            <p id='user'>Hi, {JSON.parse(localStorage.getItem("username"))}</p>
            <p>Welcome To Our Immense Website, Here we provide
              you the Best Services, Thank You.</p>
          </div>
        </div>
      </div>
    </>
  )
}
export default Home;