import React from 'react'
import CurrencyRupeeIcon from '@mui/icons-material/CurrencyRupee';
import BalanceIcon from '@mui/icons-material/Balance';
import CurrencyExchangeIcon from '@mui/icons-material/CurrencyExchange';

const Carde=({income})=>{
    return (
        <>
        <div className='card'>
            <div className='first'>
                <div>{income.inc}</div>
                <div>Income</div>
            </div>
            <div className='second'><CurrencyRupeeIcon fontSize='large'/></div>
        </div>
        <div className='card'>
            <div className='first'>
                <div>{income.exl}</div>
                <div>Expense</div>
            </div>
            <div className='second'><CurrencyExchangeIcon fontSize='large'/></div>
        </div>
        <div className='card'>
            <div className='first'>
                <div>{income.inc-income.exl}</div>
                <div>Balance</div>
            </div>
            <div className='second'><BalanceIcon fontSize='large'/></div>
        </div>
       
        </>
    )
}
export default Carde;