import React, { useEffect } from 'react'
import { useState } from 'react';
import Addexp from './addexp'
import Exp from './expense';
import Carde from './carde';
import Alert from '@mui/material/Alert';
import Stack from '@mui/material/Stack';
import FormDialog from './userdata';
import { useNavigate } from 'react-router-dom';

const Expensee = (islogin,setlogin) => {
    const navigate = useNavigate();
    useEffect(() => {
        if (!localStorage.getItem('token')) {
            navigate('/signup');
        } else {

        }
    });

    const getLocalItems = () => {
        let list = localStorage.getItem('entry');
        if (list) {
            return JSON.parse(localStorage.getItem('entry'))
        } else {
            return [];
        }
    }
    const getusername = () => {
        let list = localStorage.getItem('username');
        if (list) {
            return JSON.parse(localStorage.getItem('username'))
        } else {
            return "User";
        }
    }
    const [user, setuser] = useState(getusername());
    const [all, setall] = useState(getLocalItems());
    const [input, setinput] = useState({
        head: "",
        ledger: "",
        amt: ""
    });

    const [income, setinome] = useState({
        inc: "",
        exl: ""
    });
    const [check, setcheck] = useState(false);
    const [showalert, setshowalert] = useState({
        class: "jai",
        type: "warning",
        message: "Kindly Put All Input Values — check it out!",
        vis: "hidden"
    })

    const Showingfunction = (typee, messagee) => {
        setshowalert({
            class: "jai",
            type: typee,
            message: messagee,
            vis: "hidden"
        })
        setTimeout(() => {
            setshowalert({
                class: "jai",
                type: typee,
                message: messagee,
                vis: "visible"
            })
        }, 10);
        setTimeout(() => {
            setshowalert({
                class: "jai",
                type: "warning",
                message: "Kindly Put All Values",
                vis: "hidden"
            })
        }, 2200);
    }
    useEffect(() => {

        let sno = 1;
        let sn = document.querySelectorAll(".sno");
        for (let i = 0; i < sn.length; i++) {
            sn[i].innerText = sno;
            sno++;
        }
        let incomee = 0;
        let expe = 0;
        all.map((hi) => {
            if (hi.head === "income") {
                incomee = incomee + parseInt(hi.amt);
            }
            if (hi.head === "exp") {
                expe = expe + parseInt(hi.amt);
            }
            return hi;
        })
        setinome({
            inc: incomee,
            exl: expe
        });
        let bale = incomee - expe;
        document.title = user;
    }, [all])


    const deletee = (inde) => {
        setall((oldvale) => {
            return oldvale.filter((arr, indexx) => {
                return indexx !== inde;
            })
        })

    }

    const nameedit = () => {
        setOpen(true);
    }
    const checke = () => {
        let checking = localStorage.getItem('username');
        if (checking) {
            return false;
        } else {
            return true;
        }
    }
    const [open, setOpen] = useState(checke());
    return (
        <>
            <div className='allcontent'>
                <div className={showalert.class}>
                    <Stack sx={{ width: '100%' }} style={{ visibility: showalert.vis }} className="alert" spacing={1}>
                        <Alert variant="filled" className='hide' severity={showalert.type}>
                            {showalert.message}
                        </Alert>
                    </Stack>
                </div>

                <div className="container">
                    <h1>😍 Expense Manager - <span title='Click to Edit' style={{ cursor: "pointer" }} onClick={() => nameedit()}>{user}</span>  </h1>
                    <Addexp input={input} setinput={setinput} Showingfunction={Showingfunction} all={all} setall={setall} check={check} setcheck={setcheck} />
                    <div className='notify'><Carde income={income} /></div>
                    <div className='li'>
                        <Exp all={all} setinput={setinput} Showingfunction={Showingfunction} deletee={deletee} setcheck={setcheck} />
                    </div>
                </div>

                <FormDialog setuser={setuser} open={open} setOpen={setOpen} Showingfunction={Showingfunction} />
            </div>
        </>
    )
}
export default Expensee;