import React from 'react'
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const Error = () => {
    const navigate = useNavigate();
    
    const css = {
        fontSize: "7rem",
        textAlign: "center",
        margin: "20px 2px"
    };

 
    const fun = () => {
        navigate('/');
    }
    return (
        <>
            <div>
                <h1 style={css}>404 Error Page</h1>
                <h2 style={{ fontSize: "5rem", textAlign: "center" }}>Sorry, this page does not exist</h2>
                <h2 onClick={fun} style={{ fontSize: "3rem", textAlign: "center", cursor: "pointer" }}>Go back</h2>
            </div>
        </>
    )
}
export default Error;
