import React from 'react';
import './navbar.css'
import { NavLink } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';


const Navbar=({setlogin})=>{
   const navigate = useNavigate();
   const logout=()=>{
      setlogin(false);
      localStorage.removeItem('token');
      navigate('/signup');
   }
     return(
        <>
         <div className="nav" >
            <ul>
                <NavLink exact className={(navData) => (navData.isActive ? 'active' : '')} style={{textDecoration: 'none'}} to='/' > <li>Home</li></NavLink>
                <NavLink exact className={(navData) => (navData.isActive ? 'active' : '')} style={{textDecoration: 'none'}} to='/ExpManager' > <li>Expense Manager</li></NavLink>
                <NavLink exact className={(navData) => (navData.isActive ? 'active' : '')} style={{textDecoration: 'none'}} to='/weather' > <li>Weather app</li></NavLink>
                <NavLink exact className={(navData) => (navData.isActive ? 'active' : '')} style={{textDecoration: 'none'}} to='/covid' > <li>Covid Api</li></NavLink>
                <NavLink exact className={(navData) => (navData.isActive ? 'active' : '')} style={{textDecoration: 'none'}} to='/contact' > <li>Contact Us</li></NavLink>
                <NavLink exact className={(navData) => (navData.isActive ? 'active' : '')} style={{textDecoration: 'none'}} to='/city' > <li>City</li></NavLink>
                <NavLink exact className={(navData) => (navData.isActive ? 'active' : '')} style={{textDecoration: 'none'}} to='/users' > <li>users</li></NavLink>
                <NavLink exact className={(navData) => (navData.isActive ? 'active' : '')} style={{textDecoration: 'none'}} to='/login' > <li>Login</li></NavLink>
                <NavLink exact className={(navData) => (navData.isActive ? 'active' : '')} style={{textDecoration: 'none'}} to='/todo' > <li>Todo</li></NavLink>
                 <li onClick={logout} >Logout</li>
            </ul>
            <div className="profile">
                <span>{ JSON.parse(localStorage.getItem("username"))}</span>
            </div>
         </div>
        </>
     )
}
export default Navbar;