import React, { useEffect, useState } from 'react'
import Navbar from './navbar';
import Expensee from './exp'
import Api from './covid';
import Home from './home';
import Error from './error';
import { Routes, Route } from 'react-router-dom';
import Signup from './signup';
import Weather from './weather';
import Contact from './contactus';
import Citye from './citylist';
import Apitest from './apitest';
import Alluser from './alluser';
import Todo from './todo';

const Routee = () => {
    const [islogin,setlogin]=useState(false);
   useEffect(()=>{
    if (!localStorage.getItem('token')) {
       setlogin(false);
    } else {
       setlogin(true);
    }
   },[]);
    return (
        <>
          <Navbar setlogin={setlogin} />
       
                <Routes>
                    <Route exact path="/ExpManager" element={  <Home setlogin={setlogin} />} />
                    <Route path="/signup" element={  <Signup setlogin={setlogin} />} />
                    <Route path="/ExpManager" element={  <Expensee islogin={islogin}  setlogin={setlogin}/>} /> 
                    <Route path="/covid" element={ <Api islogin={islogin} setlogin={setlogin}/> } /> 
                    <Route path="/error" element={ <Error islogin={islogin} setlogin={setlogin}/> } /> 
                    <Route path="/weather" element={ <Weather islogin={islogin} setlogin={setlogin}/> } /> 
                    <Route path="/contact" element={ <Contact islogin={islogin} setlogin={setlogin}/> } /> 
                    <Route path="/city" element={ <Citye setlogin={setlogin} /> } /> 
                    <Route path="/login" element={ <Apitest setlogin={setlogin} /> } /> 
                    <Route path="/users" element={ <Alluser setlogin={setlogin} /> } /> 
                    <Route path="/todo" element={ <Todo setlogin={setlogin} /> } /> 
                    <Route path='*' element={ <Home setlogin={setlogin} /> } /> 
                </Routes>
                
        </>
    )
}
export default Routee;